import React, { useState } from "react";
import { makeStyles } from "@material-ui/core";
import { Transition } from "react-transition-group";

const useStyles = makeStyles((theme) => ({
  container: {
    display: "block",
    paddingTop: 32,
    paddingBottom: 16,
  },
  titleContainer: {
    display: "flex",
    alignItems: "center",
    [theme.breakpoints.down("xs")]: {
      flexDirection: "column",
      alignItems: "flex-start",
    },
  },
  title: {
    fontSize: 24,
    fontWeight: 700,
    position: "relative",
    letterSpacing: 1,
    textShadow: "3px 3px 5px rgba(0,0,0,.6)",
    zIndex: 1,
    width: "fit-content",
    color: theme.palette.text.primary,
  },
  titleDesign: {
    content: '""',
    zIndex: -1,
    left: -1,
    position: "absolute",
    height: "0.5em",
    top: "0.8em",
    width: "100%",
    transition: "0.5s",
    background: "linear-gradient(90deg,#ffd31d,#f3c623)",
    "&:hover": {
      width: "100%",
    },
  },
  "@keyframes slidein": {
    "0%": {
      opacity: 0,
    },
    "100%": {
      opacity: 1,
    },
  },
  period: {
    color: theme.palette.text.secondary,
    marginLeft: 128,
    [theme.breakpoints.down("xs")]: {
      marginLeft: 0,
    },
  },
  paragraph: {
    color: theme.palette.text.secondary,
    transition: "0.5s",
    whiteSpace: "pre-line",
    fontSize: 18,
    lineHeight: 1.8,
    "&:hover": {
      color: theme.palette.text.primary,
      transform: "translateX(20px)",
    },
  },
}));

interface ResumeBlockProps {
  title: string;
  paragraph: string;
  period: string;
}

const ResumeBlock: React.FC<ResumeBlockProps> = ({
  title,
  paragraph,
  period,
}) => {
  const classes = useStyles();
  const [inProp, setInProp] = useState(false);
  const defaultStyle = {
    transition: `width ${200}ms ease-in-out`,
    width: 0,
  };

  const transitionStyles = {
    entering: { width: "0%" },
    entered: { width: "100%" },
    exiting: { width: "100%" },
    exited: { width: "0%" },
    unmounted: { width: "0%" },
  };
  return (
    <div className={classes.container}>
      <div className={classes.titleContainer}>
        <p className={classes.title}>
          <Transition in={inProp} timeout={200}>
            {(state) => (
              <div
                className={classes.titleDesign}
                style={{
                  ...defaultStyle,
                  ...transitionStyles[state],
                }}
              />
            )}
          </Transition>
          {/* <div className={classes.titleDesign} /> */}
          {title}
        </p>
        <div className={classes.period}>{period}</div>
      </div>
      <p
        className={classes.paragraph}
        onMouseEnter={() => setInProp(true)}
        onMouseLeave={() => setInProp(false)}
      >
        {paragraph}
      </p>
    </div>
  );
};

export default ResumeBlock;
