import React, { useState } from "react";
import { makeStyles, Button } from "@material-ui/core";
import { Transition } from "react-transition-group";

const useStyles = makeStyles((theme) => ({
  container: {
    display: "block",
    paddingTop: 32,
    paddingBottom: 16,
    borderBottomColor: theme.palette.divider,
    borderBottomStyle: "solid",
    borderBlockWidth: 1,
  },
  title: {
    fontSize: 24,
    fontWeight: 700,
    position: "relative",
    letterSpacing: 1,
    textShadow: "3px 3px 5px rgba(0,0,0,.6)",
    zIndex: 1,
    width: "fit-content",
    paddingLeft: 8,
    paddingRight: 8,
    color: theme.palette.text.primary,
  },
  titleDesign: {
    content: '""',
    zIndex: -1,
    left: -1,
    position: "absolute",
    height: "0.5em",
    top: "0.8em",
    width: "100%",
    transition: "0.5s",
    background: "linear-gradient(90deg,#ffd31d,#f3c623)",
    "&:hover": {
      width: "100%",
    },
  },
  "@keyframes slidein": {
    "0%": {
      opacity: 0,
    },
    "100%": {
      opacity: 1,
    },
  },
  paragraph: {
    color: theme.palette.text.secondary,
    transition: "0.5s",
    whiteSpace: "pre-line",
    fontSize: 18,
    lineHeight: 1.8,
    "&:hover": {
      color: theme.palette.text.primary,
      transform: "translateX(20px)",
    },
  },
  block: {
    backgroundColor: "#F2A640",
    width: 200,
    maxWidth: "100%",
    height: "auto",
    maxHeight: 150,
    borderRadius: 8,
    cursor: "pointer",
    margin: 16,
    marginRight: 32,
  },
  content: {
    display: "flex",
    alignItems: "center",
    [theme.breakpoints.down("xs")]: {
      flexDirection: "column",
    },
  },
}));

function openInNewTab(href: string) {
  Object.assign(document.createElement("a"), {
    target: "_blank",
    href,
  }).click();
}

interface ResumeBlockProps {
  title: string;
  paragraph: string;
  img: string;
  link: string;
}

const ProjectBlock: React.FC<ResumeBlockProps> = ({
  title,
  paragraph,
  img,
  link,
}) => {
  const classes = useStyles();
  const [inProp, setInProp] = useState(false);
  const defaultStyle = {
    transition: `width ${200}ms ease-in-out`,
    width: 0,
  };

  const transitionStyles = {
    entering: { width: "0%" },
    entered: { width: "100%" },
    exiting: { width: "100%" },
    exited: { width: "0%" },
    unmounted: { width: "0%" },
  };
  return (
    <div className={classes.container}>
      <p className={classes.title}>
        <Transition in={inProp} timeout={200}>
          {(state) => (
            <div
              className={classes.titleDesign}
              style={{
                ...defaultStyle,
                ...transitionStyles[state],
              }}
            />
          )}
        </Transition>
        {title}
      </p>
      <div
        className={classes.content}
        onMouseEnter={() => setInProp(true)}
        onMouseLeave={() => setInProp(false)}
      >
        <img
          alt={"牛奶找工作"}
          className={classes.block}
          src={img}
          onClick={() => {
            openInNewTab("https://milk.jobs");
          }}
        />
        <p className={classes.paragraph}>{paragraph}</p>
      </div>
      <div
        style={{ width: "100%", display: "flex", justifyContent: "flex-end" }}
      >
        <Button
          variant={"contained"}
          size={"large"}
          color={"primary"}
          style={{ marginLeft: "auto", margin: 16 }}
          onClick={() => openInNewTab(link)}
        >
          {"前往網站"}
        </Button>
      </div>
    </div>
  );
};

export default ProjectBlock;
